body {
  margin: 0;
  font-family:'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: gray;
}

.App {
  height: 100vh;
}

.container {
  width: 600px;
  background: white;
  padding: 20px;
  margin: auto;
  position: relative;
  top: 50%;
  transform: translateY(-50%);
  box-shadow: 0 0 7px rgba(0,0,0,.5);
}

.col-1 {
  width: 30%;
}

.col-1 label {
  display: block;
  padding: 10px 0;
  margin: 10px 0;
  background: lightgray;
}

.col-1 {
  width: 30%;
  float: left;
  margin: 10px;
}
form:after {
  content: " ";
  display: table;
}

.col-1 label:first-child {
  text-align: center;
  background: gray;
  color: white;
}

p {
  font-style: oblique;
  font-size: 12px;
  padding: 10px;
}
input[type="submit"] {
  float: right;
}
